.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 4px solid;
  border-image: linear-gradient(to right, #52b43a, #f50057, #fcf945, #b345fc)
    47% 0%;
}

.btn {
  padding: 10px 20px;
  background-color: var(--accentColor);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
  border-radius: 4px;
  border: none;
  outline: none;
  opacity: 0.8;
  transition: opacity 250ms var(--timingFn);

  cursor: pointer;
  color: #000000;
  font-weight: 600;
  font-size: 14px;
  text-transform: uppercase;
}

.btn:hover {
  opacity: 1;
  transform: scale(1.03);
}
.nav {
  display: flex;
  justify-content: space-between;
}
